import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import LoginForm from './components/LoginForm';
import ActivityForm from './components/ActivityForm';
import ImportForm from './components/ImportForm';
import SourceURLForm from './components/SourceURLForm';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AppBar from '@material-ui/core/AppBar';

const styles = theme => ({
  root: {
    backgroundColor: '#cefbff',
    flexGrow: 1,
    position: 'relative',
    minHeight: 'calc(100vh - 120px)',
    paddingTop: 60,
    paddingBottom: 60,
  },
  header: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'center',
    left: 0,
    right: 0,
    top: 0,
    height: 60,
  },
  footer: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'center',
    left: 0,
    right: 0,
    bottom: 0,
    height: 60,
  },
  loginForm: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
  },
});

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

class Admin extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isLoading: true,
      signedIn: false,
      token: '',
      user: '',
      accessLevel: 0,
      error: '',
      submitMessage: '',
      value: 0,
    }
  }

  componentWillMount() {
    //Check if session token exists
    let jwt = localStorage.getItem("jwt");
    let token = null;
    if (jwt) {
      this.setState({
        accessLevel: JSON.parse(jwt)['permission'],
      });
      //Check if session token valid
      token = JSON.parse(jwt)['token']
      fetch('http://localhost:5000/validateUser/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          token: token
        })
      })
      .then(res => res.json())
      .then(json => {
        if(json.success) {
          //Allow access
          this.setState({
            accessLevel: JSON.parse(jwt)['permission'],
          });
        }
        else {
          //Remove session token, deny access
          localStorage.removeItem("jwt");
          this.setState({
            accessLevel: 0,
          });
        }
      });
    }
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
    });
  }

  onLogin = (username, password) => {
    fetch('http://localhost:5000/auth/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        username: username,
        password: password,
      }),
    })
    .then(res => res.json())
    .then(json => {
      console.log(json);
      if(json.status === "success"){
        this.setState({
          accessLevel: json.permission,
          user: username
        });
        localStorage.setItem("jwt", JSON.stringify(json));
      } else {
        this.setState({
          error: json.status,
        });
      }
    });
  }

  onLogout = () => {
    let jwt = localStorage.getItem("jwt");
    var token = null;
    if (jwt) {
      localStorage.removeItem("jwt");
      token = JSON.parse(jwt)['token'];
      fetch('http://localhost:5000/logout/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          token: token
        }),
      })
      .then(res => res.json())
      .then(json => {
        console.log(json);
      });
    }
    this.setState({
      accessLevel: 0,
    });
  }

  validateActivityData(activity){
    //Important question, how much validation should be done
    let validForm = true;
    //Check for null/empty strings
    for (let key in activity ) {
      if ( (activity[key] == null ||
            activity[key].length === 0 ) &&
          key != "id") {
        validForm = false;
        this.setState({
          submitMessage: "Fel! Saknas data för aktivitet.",
        });
        break;
      }
    }
    //Check for null/empty strings in the site subfield
    for (let key in activity["site"] ) {
      if (activity["site"][key] == null ||
          activity["site"][key].length === 0 ) {
        validForm = false;
        this.setState({
          submitMessage: "Fel! Saknas data för aktivitet.",
        });
        break;
      }
    }
    //Checks for timetravel errors
    if (activity["startTime"] >= activity["endTime"]) {
      validForm = false;
      this.setState({
        submitMessage: "Fel! Startiden senare än sluttiden.",
      });
    }
    return validForm;
  }

  onSubmit = (activity) => {
    let jsonActivity = JSON.stringify(activity);
    if(!this.validateActivityData(activity)){
      return;
    }
    let jwt = localStorage.getItem("jwt");
    let token = null
    if (jwt) {
      token = JSON.parse(jwt)['token']
    }
    console.log(activity);
    fetch('http://localhost:5000/addActivity', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        token: token,
        user: this.state.user,
        activity: jsonActivity,
      }),
    })
    .then(res => res.json())
    .then(json => {
      if(json.success){
        console.log("activity added")
        this.setState({
          submitMessage: "Aktivitet registrerad",
        });
      } else {
        console.log("something went wrong")
        this.setState({
          submitMessage: "Fel! Aktivitet kunde inte registreras",
        });
        this.onLogout();
      }
    });
  }

  handleChange = (event, value) => {
    this.setState({ value });
  };

  render(){
    const { classes } = this.props;
    let { accessLevel, error, value, submitMessage } = this.state;
    if(accessLevel <= 0 || accessLevel >= 3) {
      return(
        <div className={classes.root}>
          <header className={classes.header}>
            <Typography variant="h3">
              Header
            </Typography>
          </header>
          <div className={classes.loginForm}>
            <LoginForm submit={this.onLogin} error={error}/>
          </div>
          <footer className={classes.footer}>
            <Typography variant="h3">
              Footer
            </Typography>
          </footer>
        </div>
      );
    } else if(accessLevel === 1) {
      return(
        <div className={classes.root}>
          <header className={classes.header}>
            <Typography variant="h3">
              Header
            </Typography>
          </header>

          <AppBar position="static">
            <Tabs value={value} onChange={this.handleChange}>
              <Tab label="Lägg till aktivitet" />
              <Tab label="Lägg till källa" />
              <Tab label="Importera aktivitet" />
            </Tabs>
          </AppBar>
          {value === 0 && <TabContainer><ActivityForm submit={this.onSubmit}/></TabContainer>}
          {value === 1 && <TabContainer><SourceURLForm /></TabContainer>}
          {value === 2 && <TabContainer><ImportForm submit={this.onSubmit}/></TabContainer>}
          <br />
          <div>
            {submitMessage}
          </div>
          <Button variant="contained" onClick = {this.onLogout}>
            Logga ut
          </Button>
          <footer className={classes.footer}>
            <Typography variant="h3">
              Footer
            </Typography>
          </footer>
        </div>
      )
    } else if(accessLevel === 2) {
      return(
        <div className={classes.root}>
          <header className={classes.header}>
            <Typography variant="h3">
              Header
            </Typography>
          </header>
          Välkommen till Super Admin-sidan!
          <br />
          <Button variant="contained" onClick = {this.onLogout}>
            Logga ut
          </Button>
          <footer className={classes.footer}>
            <Typography variant="h3">
              Footer
            </Typography>
          </footer>
        </div>
      )
    }
  }
}
export default withStyles(styles)(Admin);
