// Formatting of individual search results
import React, { Component } from 'react';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';

class SearchResultItem extends Component {
  constructor(props){
    super(props)
    this.state = {
      isLoading: true,
    };
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
    });
  }

  render(){
    return(
      <div>
        <Typography>
          <Link href={"/product/" + this.props.data["id"]}>
            {this.props.data["name"]}: {this.props.data["ptype"]}, {this.props.data["startTime"]}
          </Link>
        </Typography>
      </div>
    );
  }
}

export default SearchResultItem;
