import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

class LoginForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isLoading: true,
      username: '',
      password: '',
      errors: {},
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleValidation = this.handleValidation.bind(this);
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
    });
  }

  handleValidation() {
    let validForm = true;
    let errors = {};
    if(!this.state.username){
      validForm = false;
      errors['username'] = "Skriv in ditt användarnamn.";
    }
    if(!this.state.password){
      validForm = false;
      errors['password'] = "Skriv in ditt lösenord.";
    }
    this.setState({
      errors: errors,
    });
    return validForm;
  }

  handleSubmit(event){
    event.preventDefault();
    let { username, password } = this.state;
    let validForm = this.handleValidation();
    console.log("Valid form: " + validForm);
    console.log("Login attempted by: " + this.state.username);
    if(validForm) {
      this.props.submit(username, password);
    }
  }

  handleChange(event){
    this.setState({
      [event.target.name]: event.target.value,
    });
  }

  render(){
    return(
      <form onSubmit={this.handleSubmit}>
        <TextField type="text" name="username" label="Användarnamn" onChange={this.handleChange} variant='outlined'/>
        <br />
        <span style={{color: "red"}}>{this.state.errors["username"]}</span>
        <br />
        <TextField type="password" name="password" label="Lösenord" onChange={this.handleChange} variant='outlined'/>
        <br />
        <span style={{color: "red"}}>{this.state.errors["password"]}</span>
        <span style={{color: "red"}}>{this.props.error}</span>
        <br />
        <Button type="submit" variant='contained' color='primary' fullWidth onClick={this.handleSubmit}>Logga in</Button>
      </form>
    );
  }
}
export default LoginForm;
