// Component showing information about an individual product

import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';

const styles = theme => ({
  root: {
    flexGrow: 1,
    borderRadius: 10,
  },
  header: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'center',
    left: 0,
    right: 0,
    top: 0,
    height: 60,
  },
  footer: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'center',
    left: 0,
    right: 0,
    bottom: 0,
    height: 60,
  },
  mainGrid: {
    backgroundColor: '#f0f0f0',
    padding: theme.spacing.unit,
    width: "100%",
  },
  topPanel: {
    //backgroundColor: 'lightblue',
    padding: theme.spacing.unit,
  },
  bottomContainer: {
    //backgroundColor: 'blue',
    padding: theme.spacing.unit,
  },
  leftPanel: {
    //backgroundColor: 'lightgreen',
    padding: theme.spacing.unit,
  },
  infoItemCenter: {
    margin: theme.spacing.unit,
    textAlign: 'center'
  },
  infoItemLeft: {

    textAlign: 'left'
  },
  infoItemRight: {

    textAlign: 'right'
  },
  bookingButton: {
    width: '70%',
  },
});

class ProductDesc extends Component {
  constructor(props){
    super(props)
    this.state = {
      isLoading: true,
      productInfo: null,
      siteInfo: null
    };
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
      productInfo: this.props.data,
    });
    console.log(this.props.data)
  }

  render(){
    const { classes, data } = this.props;
    //Protection to make sure props are loaded.
    if ( !this.state.productInfo ) {
      return (<div/>);
    }

    return(
      <div className={classes.root}>
        <Grid container className={classes.mainGrid}>
          <Grid item xs={6} className={classes.infoItemLeft}>
            <Typography variant="h3">
              {data.name}
            </Typography>
          </Grid>
          <Grid item xs={6} className={classes.infoItemRight}>
            <Typography variant="h3">
              {data.site["name"]}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <br/>
          </Grid>
          <Grid item xs={12} className={classes.infoItemLeft}>
            <Typography variant="body1">
              {data.pdescription}
            </Typography>
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default withStyles(styles)(ProductDesc);


