import React, { Component } from 'react';
//import './App.css';
import { withStyles } from '@material-ui/core/styles';
import SearchResults from './components/SearchResults';
import Search from './components/Search';
import Typography from '@material-ui/core/Typography';
import MapView from './components/MapView';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import ContainerDimensions from 'react-container-dimensions';

const styles = theme => ({
  root: {
    backgroundColor: '#f0f0f0',
    flexGrow: 1,
    position: 'relative',
    minHeight: 'calc(100vh - 120px)',
    paddingTop: 60,
    paddingBottom: 60,
  },
  header: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'left',
    left: 0,
    right: 0,
    top: 0,
    height: 60,
  },
  footer: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'left',
    left: 0,
    right: 0,
    bottom: 0,
    height: 60,
  },
  mainGrid: {
    backgroundColor: 'white',
    minWidth: '400px',
    maxHeight: 'calc(100vh - 120px)',
    width: '80%',
    marginLeft: '10%',
    marginRight: '10%',
    marginTop: theme.spacing.unit,
  },
  searchAndResults: {
    //backgroundColor: 'lightgreen',
    padding: theme.spacing.unit,
    height: '100%',
  },
  searchContainer: {
    //backgroundColor: 'blue',
    padding: theme.spacing.unit,
    height: '100%'
  },
  resultsContainer: {
    //backgroundColor: 'green',
    padding: theme.spacing.unit,
    height: '100%',
  },
  mapContainer: {
    //backgroundColor: 'blue',
    //position: 'relative',
    flexGrow: '1',
    padding: theme.spacing.unit,
  },
});

class Results extends Component {
  constructor(props){
    super(props)
    this.state = {
      isLoading: true,
      searchResult: [],
      title: "Sökresultat",
      location: null,
      activity: null,
    }
  }
  componentDidMount(){
    this.doSearch();
    this.setState({
      location: this.props.match.params.stad,
      activity: this.props.match.params.aktivitet,
      isLoading: false
    });
  }

  static getDerivedStateFromProps(nextProps, prevState) {
   if (nextProps.match.params.stad !== prevState.city || nextProps.match.params.aktivitet !== prevState.activity){
      const currentActivity = nextProps.match.params.aktivitet
      const currentLocation = nextProps.match.params.stad
      return {
        activity: currentActivity,
        city: currentLocation,
      }
    }
    return null;
  }

  componentDidUpdate(prevProps, prevState) {
    if(prevProps.location.pathname !== this.props.location.pathname){
      this.doSearch();
      return;
    }
  }

  doSearch() {
    let activity = this.props.match.params.aktivitet;
    let location = this.props.match.params.stad;
    let request = '?search='
    if (activity !== 'Vad') {
      request += activity;
    };
    request += '&city=';
    if (location !== 'Var') {
      request += location;
    };
    fetch(
      'http://localhost:5000/search/' + request)
      .then(res => res.json())
      .then(json => {
        this.setState({
          searchResult: json
        });
      });
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <header className={classes.header}>
          <Link href="/" variant="h3" color="textPrimary">
            hittaträning.se
          </Link>
        </header>
        <Grid container className={classes.mainGrid}>
          <Grid container item className={classes.searchAndResults} xs={3}>
            <Grid item className={classes.searchContainer} xs={12}>
              <Search type='results'/>
            </Grid>
            <Grid item className={classes.resultsContainer} xs={12}>
              <SearchResults data={this.state.searchResult}/>
            </Grid>
          </Grid>
          <Grid item className={classes.mapContainer} xs={9}>
            <ContainerDimensions>
              <MapView data={this.state.searchResult} location={this.props.match.params.stad}/>
            </ContainerDimensions>
          </Grid>
        </Grid>
        <footer className={classes.footer}>
          <Typography variant="h3">
            Footer
          </Typography>
        </footer>
      </div>
    );
  }
}

export default withStyles(styles)(Results);
