// Component showing information about an individual product

import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import MapView from './components/MapView';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import ContainerDimensions from 'react-container-dimensions';
import ProductInfo from './components/ProductInfo';
import ProductDesc from './components/ProductDesc';

const styles = theme => ({
  root: {
    backgroundColor: '#eaeeef',
    flexGrow: 1,
    position: 'relative',
    minHeight: 'calc(100vh - 120px)',
    paddingTop: 60,
    paddingBottom: 60,
  },
  header: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'left',
    left: 0,
    right: 0,
    top: 0,
    height: 60,
    textDecoration: 'none',
  },
  footer: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'center',
    left: 0,
    right: 0,
    bottom: 0,
    height: 60,
  },
  mainGrid: {
    backgroundColor: 'white',
    minHeight: 'calc(100vh - 120px)',
    height: '100%',
    width: '60%',
    marginLeft: '20%',
    marginRight: '20%',
    padding: theme.spacing.unit,
  },
  topPanel: {
    //backgroundColor: 'lightblue',
    //padding: theme.spacing.unit,
  },
  bottomContainer: {
    //backgroundColor: 'blue',
    //padding: theme.spacing.unit,
  },
  leftPanel: {
    //backgroundColor: 'lightgreen',
    //padding: theme.spacing.unit,
  },
  mapContainer: {
    //backgroundColor: 'lightblue',
    padding: theme.spacing.unit,
    maxHeight: 'calc(100vh * 0.57)',
    position: 'relative',
    top: 0,
  },

});

class Product extends Component {
  constructor(props){
    super(props)
    this.state = {
      isLoading: true,
      productInfo: null,
      siteInfo: null
    };
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
    });
    fetch('http://localhost:5000/getWorkout/?id=' + this.props.match.params.id)
        .then(res => res.json())
        .then(json => {
          this.setState({
            productInfo: json,
            siteInfo: json.site
          })
        });
  }

  render(){
    const { classes } = this.props;
    //Protection to make sure props are loaded.
    if ( !this.state.productInfo ) {
      return (<div/>);
    }

    return(
      <div className={classes.root}>
        <header className={classes.header}>
          <Link href="/" variant="h3" color="textPrimary">
            hittaträning.se
          </Link>
        </header>
        <Grid container className={classes.mainGrid}>
          <Grid item className={classes.topPanel} xs={12}>
            <ProductDesc data={this.state.productInfo} />
          </Grid>
          <Grid container item className={classes.bottomContainer} xs={12}>
            <Grid item className={classes.leftPanel} xs={6}>
              <ProductInfo data={this.state.productInfo} />
            </Grid>
            <Grid item className={classes.mapContainer} xs={6}>
                <MapView
                  data={[this.state.productInfo]}
                  location={this.state.siteInfo["city"]}
                  />
            </Grid>
          </Grid>
        </Grid>
        <footer className={classes.footer}>
          <Typography variant="h3">
            Footer
          </Typography>
        </footer>
      </div>
    );
  }
}

export default withStyles(styles)(Product);


