import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

import DateTimePicker from 'react-datetime-picker';

class ActivityForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isLoading: true,
      sDate: new Date(),
      eDate: new Date(),
      startTime: null,
      endTime: null,
      activity: {
        available: null,
        description: null,
        url: null,
        price: null,
        site: {
          city: null,
          address: null,
          zipCode: null,
          name: null,
        },
        ptype: null,
        pdescription: null,
        startTime: null,
        duration: null,
        endTime: null,
        type: null,
        name: null,
      },
      coolList: [
        ["Namn", "name"],
        ["Typ", "type"],
        ["Produktyp", "ptype"],
        ["Beskrivning", "description"],
        ["Produktbeskrivning", "pdescription"],
        ["Antal platser", "available"],
        ["Passlängd", "duration"],
        ["Pris", "price"],
        ["URL", "url"],
        ["Anläggning", "siteName"],
        ["Gata", "address"],
        ["Stad", "city"],
        ["Postnummer", "zipCode" ],
      ],
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
    });
  }

  handleSubmit(event){
    event.preventDefault();
    let { date, activity, inputMessage, sDate, eDate } = this.state;
    let { startTime, endTime, duration } = this.state.activity;

    sDate.setSeconds(0);
    eDate.setSeconds(0);

    startTime = sDate.toISOString();
    endTime = eDate.toISOString();

    if (date !== null) {
      if (startTime !== null) {
        activity.startTime = startTime.substr(0,10) + " " + startTime.substr(11,8);
      }
      if (endTime !== null) {
        activity.endTime = endTime.substr(0,10) + " " + endTime.substr(11,8);
      }
    }

    //Clean seconds/milliseconds
    sDate.setMilliseconds(0);
    eDate.setMilliseconds(0);
    sDate.setSeconds(0);
    eDate.setSeconds(0);

    //Calculate duration, hours
    activity.duration = (eDate - sDate)/3600000;

    this.props.submit(activity);
  }

  // keeps the activity up to date
  // a few special cases so we can make the UI more user friendly
  handleChange(event){
    if(event.target.name === "siteName" ) {
      this.setState({
        activity: {
          ...this.state.activity,
          site: {
            ...this.state.activity.site,
            name: event.target.value,
          },
        },
      });
    } else if(event.target.name === "city" ||
              event.target.name === "zipCode" ||
              event.target.name === "address") {
      this.setState({
        activity: {
          ...this.state.activity,
          site: {
            ...this.state.activity.site,
            [event.target.name]: event.target.value,
          },
        },
      });
    } else {
      this.setState({
        activity: {
          ...this.state.activity,
          [event.target.name]: event.target.value,
        }
      });
    }
  }

  onStartChange = sDate => this.setState({ sDate });
  onEndChange = eDate => this.setState({ eDate });

  render(){
    return(
      <Grid container>
        <div>
          Aktivitetsinformation:
        </div>
        { this.state.coolList.map(item => (
          <Grid item xs={12} key={item}>
            <TextField variant="outlined" label={item[0]} name={item[1]} onChange={this.handleChange}/>
            <br />
          </Grid>
        ))}
        <Grid item xs={12}>
          <div>
            Börjar:
          </div>
          <DateTimePicker
            onChange={this.onStartChange}
            value={this.state.sDate}
            clearIcon={null}
          />
        </Grid>
        <Grid item xs={12}>
          <div>
            Slutar:
          </div>
          <DateTimePicker
            onChange={this.onEndChange}
            value={this.state.eDate}
            clearIcon={null}
          />
        </Grid>
        <Grid item>
          <Button variant="contained" color="primary" fullWidth onClick={this.handleSubmit}>Lägg till</Button>
        </Grid>

      </Grid>
    );
  }
}
export default ActivityForm;

