import React, { Component } from 'react';
//import './App.css';
import Typography from '@material-ui/core/Typography';
import Search from './components/Search';
import SearchLinks from './components/SearchLinks';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';

const styles = theme => ({
  root: {
    backgroundColor: '#f0f0f0',
    flexGrow: 1,
    position: 'relative',
    minHeight: 'calc(100vh - 120px)',
    paddingTop: 60,
    paddingBottom: 60,
  },
  header: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'left',
    left: 0,
    right: 0,
    top: 0,
    height: 60,
  },
  footer: {
    backgroundColor: 'pink',
    position: 'absolute',
    textAlign: 'left',
    left: 0,
    right: 0,
    bottom: 0,
    height: 60,
  },
  mainGrid: {
    backgroundColor: 'white',
    minHeight: 'calc(100vh - 120px)',
    minWidth: '400px',
    height: '100%',
    width: '60%',
    marginLeft: '20%',
    marginRight: '20%',

  },
  searchContainer: {
    //backgroundColor: '#f0f0f0',
    marginTop: '10%',
    maxWidth: '80%',
  },
  linksContainer: {
    //backgroundColor: '#f0f0f0',
    maxWidth: '80%'
  },
});

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      isLoading: true,
      title: "hittaträning.se",
      searchTitle: "Hitta träningspass!",
      popularSearches: "Populära sökningar",
    }
  }
  componentDidMount(){
    this.setState({
      isLoading: false,
    });
  }


  render() {
	const { classes } = this.props;
	const { isLoading } = this.state;
	if(isLoading) {
	  return (
		<Grid className={classes.root}>
		  <CircularProgress />
		</Grid>
	  );
	} else {
	  return (
		<div className={classes.root}>
		  <header className={classes.header}>
			<Typography variant="h3">
			  {this.state.title}
			</Typography>
		  </header>
		  <Grid container spacing={24} className={classes.mainGrid} justify={'center'} alignItems={'baseline'}>
			<Grid item className={classes.searchContainer} xs={12}>
			  <Search type='main' title={this.state.searchTitle}/>
			</Grid>
			<Grid item className={classes.linksContainer} xs={12}>
			  <SearchLinks title={this.state.popularSearches}/>
			</Grid>
		  </Grid>
		  <footer className={classes.footer}>
			<Typography variant="h3">
			  Footer
			</Typography>
		  </footer>
		</div>
	  );
	}
  }
}

export default withStyles(styles)(App);
